.searchFieldsetAdvanced {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 8px; }

@media (min-width: 768px) {
  .searchFieldsetAdvanced {
    padding-left: 0; } }

.searchFieldsetAdvanced__group {
  width: 45%; }

.searchFieldsetAdvanced__group:hover {
  background-color: #fff;
  cursor: pointer; }

.searchFieldsetAdvanced__postcode {
  width: 20%; }

.searchFieldsetAdvanced__distance {
  width: 45%; }

.searchFieldsetAdvanced__distance:hover {
  background-color: #fff;
  cursor: pointer; }
