._menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around; }

@media (min-width: 992px) {
  ._menu {
    padding: 0; } }

._menu__messages, ._menu__notifications {
  display: flex;
  font-size: 12px;
  font-weight: 300;
  height: 32px;
  white-space: nowrap;
  border-radius: 3px;
  padding: 8px;
  color: #116db4; }
  ._menu__messages:hover, ._menu__notifications:hover {
    cursor: pointer; }
  @media (min-width: 992) {
    ._menu__messages, ._menu__notifications {
      padding: 8px; } }
  ._menu__messages:hover, ._menu__notifications:hover {
    background-color: #f7f7f6;
    color: #1589e3; }
  ._menu__messagesIcon, ._menu__notificationsIcon {
    padding: 0; }

@media (min-width: 992px) {
  ._menu__messagesIcon, ._menu__notificationsIcon {
    padding: 0 12px 0 0; } }

._menu__placeAd {
  display: flex;
  font-size: 12px;
  font-weight: 300;
  height: 32px;
  white-space: nowrap;
  border-radius: 3px;
  padding: 8px 12px 8px 8px;
  color: #fff;
  background-color: #137bcb; }
  ._menu__placeAd:hover {
    cursor: pointer; }
  @media (min-width: 992) {
    ._menu__placeAd {
      padding: 8px; } }
  ._menu__placeAd:hover {
    background: #1589e3;
    box-shadow: inset 0 -2px 0 0 #137bcb;
    color: #fff; }
  ._menu__placeAdIcon {
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    -webkit-filter: FlipH;
    filter: FlipH;
    color: #fff;
    padding: 0; }

@media (min-width: 992px) {
  ._menu__placeAdIcon {
    padding: 0 8px; } }

._menu__placeAdButton {
  font-family: BreeSerif, sans-serif;
  font-size: 14px;
  -ms-flex-item-align: center;
  align-self: center; }

._menu__messagesLabel, ._menu__notificationsLabel, ._menu__placeAdButton {
  display: none; }

@media (min-width: 992px) {
  ._menu__messagesLabel, ._menu__notificationsLabel, ._menu__placeAdButton {
    display: block; } }
